.DataTable {
  position: relative;
  border: solid 1px hsl(200, 10%, 40%);
  background-color: hsl(200, 10%, 23%);
}
.DataTable * {
  scrollbar-color: hsl(200, 10%, 28%) hsl(200, 10%, 18%);
}
.DataTable ::-webkit-scrollbar {
  background-color: hsl(200, 10%, 18%);
}
.DataTable ::-webkit-scrollbar-track {
  background-color: hsl(200, 10%, 18%);
}
.DataTable ::-webkit-scrollbar-thumb {
  background-color: hsl(200, 10%, 28%);
}
.DataTable ::-webkit-scrollbar-corner {
  background-color: hsl(200, 10%, 18%);
}
.DataTable-header {
  user-select: none;
  background-color: hsl(200, 10%, 32%);
  font-weight: bold;
}
.DataTable-header .DataTable-cell {
  border-right: solid 1px hsl(200, 10%, 40%);
}
.DataTable-header .DataTable-cell.sortable {
  cursor: pointer;
}
.DataTable-header .DataTable-cell .DataTable-sort-indictator {
  fill: currentColor;
}
.DataTable-cell {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  padding: 0 5px;
  border-bottom: solid 1px hsl(200, 10%, 40%);
  border-right: solid 1px hsl(200, 10%, 29%);
}
.DataTable-cell.last-in-row {
  border-right: solid 1px hsl(200, 10%, 40%);
}
.DataTable-cell.number {
  justify-content: flex-end;
}
.DataTable-cell.footer-cell {
  font-weight: bold;
}
.DataTable-cell.custom {
  padding: 0;
}
.DataTable-cell > .DataTable-cell-text {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.DataTable-cell > .ant-time-picker {
  /* Cover the default borders. */
  margin: -1px;
  height: calc(100% + 2px);
  /* Make sure the glowing border is entirely visible. */
}
.DataTable-cell > .ant-time-picker:hover {
  z-index: 10;
}
.DataTable-cell > .ant-time-picker input {
  height: 100%;
}
.DataTable-no-result {
  margin: 20px;
  color: hsl(200, 20%, 80%);
}
