.BigSelect.Select > .Select-control {
  cursor: pointer;
  background-color: hsl(200, 10%, 20%);
  color: hsl(200, 10%, 90%);
  height: 32px;
  border-color: hsl(200, 10%, 40%);
  border-radius: 2px;
}
.BigSelect.Select .Select-control .Select-value .Select-value-label {
  color: white !important;
}
.BigSelect.Select .Select-placeholder,
.BigSelect.Select .Select--single > .Select-control .Select-value {
  line-height: 32px;
}
.BigSelect.Select .Select-input {
  height: 30px;
}
.BigSelect.Select:hover > .Select-control {
  border-color: #29bfff;
}
.BigSelect.Select.is-focused > .Select-control {
  background-color: hsl(200, 10%, 20%);
  border-color: #29bfff;
}
.BigSelect.Select.is-focused:not(.is-open) > .Select-control {
  background-color: hsl(200, 10%, 20%);
  border-color: #29bfff;
}
.BigSelect.Select > .Select-menu-outer {
  margin-top: 0;
  background-color: hsl(200, 10%, 20%);
  border-color: hsl(200, 10%, 40%);
}
