.v-m-AnimationSelectionComponent {
    margin: 0 10px;

    & > ul {
        height: 100%;
        padding: 0;
        margin: 0;
        overflow-y: scroll;
        list-style-type: none;

        & > li {
            cursor: pointer;
            padding: 2px 5px;
            white-space: nowrap;

            &.selected {
                color: lighten(@primary-color, 15%);
            }

            &:hover {
                color: lighten(@primary-color, 30%);
            }
        }
    }
}

@primary-color: hsl(200, 100%, 50%);@white: #000;@black: #fff;@primary-1: fade(@primary-color, 50%);@primary-2: fade(@primary-color, 40%);@body-background: hsl(200, 10%, 20%);@component-background: @body-background;@text-color: hsl(200, 10%, 90%);@text-color-secondary: hsl(200, 20%, 80%);@text-color-dark: fade(white, 85%);@text-color-secondary-dark: fade(white, 65%);@heading-color: fade(@black, 85%);@border-radius-base: 2px;@border-radius-sm: 0px;@background-color-light: lighten(@component-background, 20%);@background-color-base: fade(@primary-color, 20%);@item-active-bg: fade(@primary-color, 20%);@item-hover-bg: fade(@primary-color, 10%);@border-color-base: lighten(@component-background, 20%);@border-color-split: lighten(@component-background, 10%);@disabled-color: fade(#fff, 50%);@animation-duration-slow: 0.1s;@animation-duration-base: 0.066s;@animation-duration-fast: 0.033s;@input-bg: darken(@component-background, 5%);@btn-default-bg: lighten(@component-background, 10%);@modal-mask-bg: fade(black, 80%);@table-selected-row-bg: @item-active-bg;@table-row-hover-bg: @item-hover-bg;@menu-dark-bg: @component-background;