.v-ViewerComponent {
  display: flex;
  padding-top: 10px;
  overflow: hidden;
}
.v-ViewerComponent > .ant-tabs {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.v-ViewerComponent > .ant-tabs > .ant-tabs-content {
  flex: 1;
  overflow: hidden;
}
.v-ViewerComponent > .ant-tabs > .ant-tabs-content > .ant-tabs-tabpane-active {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
