@import "./theme.less";

.DataTable {
    // position: relative; necessary to avoid background and border dissappearing while antd
    // animates dropdowns in Chrome. No idea why this prevents it...
    position: relative;
    border: solid 1px @border-color-base;
    background-color: lighten(@component-background, 3%);

    & * {
        scrollbar-color: @table-scrollbar-thumb-color @table-scrollbar-color;
    }

    & ::-webkit-scrollbar {
        background-color: @table-scrollbar-color;
    }

    & ::-webkit-scrollbar-track {
        background-color: @table-scrollbar-color;
    }

    & ::-webkit-scrollbar-thumb {
        background-color: @table-scrollbar-thumb-color;
    }

    & ::-webkit-scrollbar-corner {
        background-color: @table-scrollbar-color;
    }
}

.DataTable-header {
    user-select: none;
    background-color: lighten(@component-background, 12%);
    font-weight: bold;

    & .DataTable-cell {
        border-right: solid 1px @border-color-base;

        &.sortable {
            cursor: pointer;
        }

        & .DataTable-sort-indictator {
            fill: currentColor;
        }
    }
}

.DataTable-cell {
    display: flex;
    align-items: center;
    box-sizing: border-box;
    padding: 0 5px;
    border-bottom: solid 1px @border-color-base;
    border-right: solid 1px darken(@border-color-base, 11%);

    &.last-in-row {
        border-right: solid 1px @border-color-base;
    }

    &.number {
        justify-content: flex-end;
    }

    &.footer-cell {
        font-weight: bold;
    }

    &.custom {
        padding: 0;
    }

    & > .DataTable-cell-text {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
    
    & > .ant-time-picker {
        /* Cover the default borders. */
        margin: -1px;
        height: calc(100% + 2px);

        /* Make sure the glowing border is entirely visible. */
        &:hover {
            z-index: 10;
        }

        & input {
            height: 100%;
        }
    }
}

.DataTable-no-result {
    margin: 20px;
    color: @text-color-secondary;
}
