.v-m-ModelViewerComponent {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}
.v-m-ModelViewerComponent-toolbar {
  display: flex;
  padding: 10px 5px;
  align-items: center;
}
.v-m-ModelViewerComponent-toolbar > * {
  margin: 0 5px;
}
.v-m-ModelViewerComponent-toolbar .group {
  display: flex;
  align-items: center;
}
.v-m-ModelViewerComponent-toolbar .group > * {
  margin: 0 5px;
}
.v-m-ModelViewerComponent-main {
  flex: 1;
  display: flex;
  overflow: hidden;
}
.v-m-ModelViewerComponent-main > div:nth-child(3) {
  flex: 1;
}
