.v-m-AnimationSelectionComponent {
  margin: 0 10px;
}
.v-m-AnimationSelectionComponent > ul {
  height: 100%;
  padding: 0;
  margin: 0;
  overflow-y: scroll;
  list-style-type: none;
}
.v-m-AnimationSelectionComponent > ul > li {
  cursor: pointer;
  padding: 2px 5px;
  white-space: nowrap;
}
.v-m-AnimationSelectionComponent > ul > li.selected {
  color: hsl(200, 100%, 65%);
}
.v-m-AnimationSelectionComponent > ul > li:hover {
  color: hsl(200, 100%, 80%);
}
